function BottomRow(props) {
    let c = ["Awaken", "Confuse", "Darkness", "Disrupt", "Echo", "Endure", "Fear", 
    "Foresight", "Invisibility", "Plague", "Restore", "Scorch", "Terraform", "Transmute", "TimeStop"];


    shuffle(c);
        
    function shuffle(c) {
      for (let i = c.length - 1; i > 0; i--) {
          const j = Math.floor(Math.random() * (i + 1));
          [c[i], c[j]] = [c[j], c[i]];
      }
      return c.splice(5);
    }
    

    let imagesSpells = c.map(image => {
       return <img key={image} src={require(`../pngs/${image}.png`)} alt="" className="img-responsive" 
       style={{width:"30%", float:"left"}}
       />
    
 
   });
    return (

<div className="row" id="bottomRow"
style={{WebkitBorderRadius:2, WebkitTextStrokeColor:
"red", float:"left"}}
>
  <div className="col-6-md" id="spellBacks">
  <img src={require('../pngs/SpellBacks.png')} alt="SpellBacks"
    style={{width:"30%", float:"left"}}/></div>
     <div className="col-6-md">
            {/* <img src={require('../pngs/Honey.png')} alt="Honey"
              style={{width:"15%", float:"left"}}
              /> */}
{imagesSpells}
            </div>
{/* 
            <div className="col-6-md">
            <img src={require('../pngs/Honey.png')} alt="Honey"
              style={{width:"15%", float:"left"}}/>
            </div>

            <div className="col-6-md">
            <img src={require('../pngs/Honey.png')} alt="Honey"
              style={{width:"15%", float:"left"}}/></div>
          
          <div className="col-6-md">
            <img src={require('../pngs/Honey.png')} alt="Honey"
              style={{width:"15%", float:"left"}}/>
            </div>

            <div className="col-6-md">
            <img src={require('../pngs/Honey.png')} alt="Honey"
              style={{width:"15%", float:"left"}}/></div> */}
          
</div>);
}
export default BottomRow;
