import React, { useState, useEffect } from "react";

// The deck of cards is probably a constant whose values 
// never change directly. It's possible that I'm wrong and the deck 
// *does* change, but even so I imagine it would come from a prop or
//  context from a parent component. Either way the cards array should not be mutable.
const CARDS = [
  "Honey0",
  "Bone0",
  "Herbs0",
  "Mushroom0",
  "Seeds0",
  "Honey1",
  "Bone1",
  "Herbs1",
  "Mushroom1",
  "Seeds1",
  "Honey2",
  "Bone2",
  "Herbs2",
  "Mushroom2",
  "Seeds2",
  "Honey3",
  "Bone3",
  "Herbs3",
  "Mushroom3",
  "Seeds3",
  "Honey4",
  "Bone4",
  "Herbs4",
  "Mushroom4",
  "Seeds4",
  "Honey5",
  "Bone5",
  "Herbs5",
  "Mushroom5",
  "Seeds5",
  "Honey6",
  "Bone6",
  "Herbs6",
  "Mushroom6",
  "Seeds6",
  "Honey7",
  "Bone7",
  "Herbs7",
  "Mushroom7",
  "Seeds7",
  "Honey8",
  "Bone8",
  "Herbs8",
  "Mushroom8",
  "Seeds8",
  "Honey9",
  "Bone9",
  "Herbs9",
  "Mushroom9",
  "Seeds9"
];

const initialCards = [];

const cardStyles = {
  padding: 10,
  display: "block",
  flexShrink: 0,
  flexGrow: 0,
  fontSize: 12,
  width: "calc(33.325% - 19.925px)"
};

function TopRow(props) {
  // Keep the current hand of cards in state rather than mutating an array directly in the function body. React function components should be pure, with all side effects ocurring inside of effect hooks.
  let [cards, setCards] = useState(initialCards);
  let [honeyCount, setHoneyCount] = useState(
    countSubstrings(initialCards, "Honey")
  );
  let [seedsCount, setSeedsCount] = useState(
    countSubstrings(initialCards, "Seeds")
  );
  let [herbsCount, setHerbsCount] = useState(
    countSubstrings(initialCards, "Herbs")
  );

  let [mushroomCount, setMushroomCount] = useState(
    countSubstrings(initialCards, "Mushroom")
  );
  let [boneCount, setBoneCount] = useState(
    countSubstrings(initialCards, "Bone")
  );


  let imageIngredients = cards.map(image => (
    <img
      key={image}
      src={require(`../pngs/${image}.png`)}
      alt="ingredients" className="img-responsive"

      // You don't have to do this exactly, but human readable alt text is important if the context is important for the functionality of the app, which it is in this case
      alt={`Ingredient card ${image
        .replace(/\d/, n => ` ${n}`)
        .replace(/\s0?(0+)/, " ")}`}
      className="img-responsive"
      style={{
        //width: "15%",
        //float: "left",

        // just for demo purposes here since I don't have your images
        ...cardStyles,
        padding: 1,
        border: `2px solid ${image.includes("Honey") ? "goldenrod" : "red"}`
      }}
    />
  ));

  function shuffleCards() {
    setCards(shuffleArray(CARDS));
  }

  function reset() {
    setCards(initialCards);
    setHoneyCount(countSubstrings(initialCards, "Honey"));
    setSeedsCount(countSubstrings(initialCards, "Seeds"));
    setHerbsCount(countSubstrings(initialCards, "Herbs"));
    setMushroomCount(countSubstrings(initialCards, "Mushrooms"));
    setBoneCount(countSubstrings(initialCards, "Bone"));
  }
  // Any time our cards are updated, we want to increment the
  // number of Honey cards in our hand.
  useEffect(() => {
    setHoneyCount(count => count + countSubstrings(cards, "Honey"));
  }, [cards]);

  useEffect(() => {
    setSeedsCount(count => count + countSubstrings(cards, "Seeds"));
  }, [cards]);

  useEffect(() => {
    setHerbsCount(count => count + countSubstrings(cards, "Herbs"));
  }, [cards]);
  
  useEffect(() => {
    setMushroomCount(count => count + countSubstrings(cards, "Mushroom"));
  }, [cards]);

  
  useEffect(() => {
    setBoneCount(count => count + countSubstrings(cards, "Bone"));
  }, [cards]);


  return (
    <div
      {...props}
      className="row"
      id="topRow"
      style={
        {
          // WebkitBorderRadius: 2,
          // WebkitTextStrokeColor: "red",
          // width: "90%",
          // maxHeight: "30%",
          // padding: 0
        }
      }
    >
      <button onClick={shuffleCards}>
        {cards.length ? "Shuffle" : "Deal"}
      </button>
      <button onClick={reset}>Reset</button>
      <hr />
      <div className="col-6-md">
        <img
          src={require("../pngs/IngredientBacks.png")}
          alt="Back of ingredient card"
          style={{
            // width: "15%",
            // float: "left",

            // just for demo purposes here since I don't have your images
            ...cardStyles,
            margin: 1,
            border: "2px solid blue"
          }}
        />
      </div>

      <div
        className="col-6-md"
        style={{
          display: "flex",
          flexWrap: "wrap",
          justifyContent: "space-between",
          padding: 1
        }}
      >
        {imageIngredients}
      </div>
      <hr />
      <div>
        <strong>Honey:</strong> {honeyCount}
      </div>
      <div>
        <strong>Seeds:</strong> {seedsCount}
      </div>
      <div>
        <strong>Herbs:</strong> {herbsCount}
      </div>
      <div>
        <strong>Mushrooms:</strong> {mushroomCount}
      </div>
      <div>
        <strong>Bone:</strong> {boneCount}
      </div>
    </div>
  );
}
export default TopRow;

// I put these utility functions outside of the component body since there is no real reason to recreate them on each render.

/**
 * @param {any[]} array
 */
function shuffleArray(array) {
  for (let i = array.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1));
    [array[i], array[j]] = [array[j], array[i]];
  }
  // Use slice instead of splice here to prevent mutating the original array
  return array.slice(0, 5);
}

/**
 * @param {string[]} array
 * @param {string} subs
 */
function countSubstrings(array, subs) {
  return array.filter(card => card.includes(subs)).length;
}
