import React, { useState, useEffect } from "react";

// import "../style.css"


const CARDS = [
  "Extract0", "Essence0", "Incense0", "Poison0", "Tonic0",
    "Extract1", "Essence1", "Incense1", "Poison1", "Tonic1",
    "Extract2", "Essence2", "Incense2", "Poison2", "Tonic2",
    "Extract3", "Essence3", "Incense3", "Poison3", "Tonic3"
];
 

const initialCards = [];

const cardStyles = {
  padding: 10,
  display: "block",
  flexShrink: 0,
  flexGrow: 0,
  fontSize: 12,
  width: "calc(30.325% - 19.925px)"
};


function MidRow(props) {
  let [cards, setCards] = useState(initialCards);
  let [extractCount, setExtractCount] = useState(
    countSubstrings(initialCards, "Extract")
  );


  let [poisonCount, setPoisonCount] = useState(
    countSubstrings(initialCards, "Poison")
  );
  let [essenceCount, setEssenceCount] = useState(
    countSubstrings(initialCards, "Essence")
  );
  let [incenseCount, setIncenseCount] = useState(
    countSubstrings(initialCards, "Incense")
  );
  let [tonicCount, setTonicCount] = useState(
    countSubstrings(initialCards, "Tonic")
  );   






let imagePotions = cards.map(image => (
  <img
    key={image}
    src={require(`../pngs/${image}.png`)}
    alt="potions" className="img-responsive"

    // You don't have to do this exactly, but human readable alt text is important if the context is important for the functionality of the app, which it is in this case
    alt={`Potion card ${image
      .replace(/\d/, n => ` ${n}`)
      .replace(/\s0?(0+)/, " ")}`}
    className="img-responsive"
    style={{
      //width: "15%",
      //float: "left",

      // just for demo purposes here since I don't have your images
      ...cardStyles,
      padding: 10,
      border: `2px solid ${image.includes("Extract") ? "goldenrod" : "red"}`
    }}
  />
  ));
   


   
  function shuffleCards() {
    setCards(shuffleArray(CARDS));
  }

  function reset() {
    setCards(initialCards);
    setExtractCount(countSubstrings(initialCards, "Extract"));
    setEssenceCount(countSubstrings(initialCards, "Essence"));
    setIncenseCount(countSubstrings(initialCards, "Incense"));
    setPoisonCount(countSubstrings(initialCards, "Poison"));
    setTonicCount(countSubstrings(initialCards, "Tonic"));
  }

  // Any time our cards are updated, we want to increment the
  // number of Extract cards in our hand.
  useEffect(() => {
    setExtractCount(count => count + countSubstrings(cards, "Extract"));
  }, [cards]);

  useEffect(() => {
    setEssenceCount(count => count + countSubstrings(cards, "Essence"));
  }, [cards]);
  useEffect(() => {
    setIncenseCount(count => count + countSubstrings(cards, "Incense"));
  }, [cards]);
  useEffect(() => {
    setPoisonCount(count => count + countSubstrings(cards, "Poison"));
  }, [cards]);
  useEffect(() => {
    setTonicCount(count => count + countSubstrings(cards, "Tonic"));
  }, [cards]);

  return (
    <div
      {...props}
      className="row"
      id="midRow"
      style={
        {
          // WebkitBorderRadius: 2,
          // WebkitTextStrokeColor: "red",
          // width: "90%",
          // maxHeight: "30%",
          // padding: 0
        }
      }
    >
      <button onClick={shuffleCards}>
        {cards.length ? "Shuffle" : "Deal"}
      </button>
      <button onClick={reset}>Reset</button>
      <hr />
      <div className="col-6-md">
        <img
          src={require("../pngs/IngredientBacks.png")}
          alt="Back of potion card"
          style={{
            // width: "15%",
            // float: "left",

            // just for demo purposes here since I don't have your images
            ...cardStyles,
            margin: 1,
            border: "2px solid blue"
          }}
        />
      </div>

      <div
        className="col-6-md"
        style={{
          display: "flex",
          flexWrap: "wrap",
          justifyContent: "space-between",
          padding: 1
        }}
      >
        {imagePotions}
      </div>
      <hr />
      <div>
        <strong>Extract:</strong> {extractCount}
    
        <strong> Essence:</strong> {essenceCount}
       
        <strong> Incense:</strong> {incenseCount}
     
        <strong> Tonic:</strong> {tonicCount}
     
        <strong> Poison:</strong> {poisonCount}
      </div>
    </div>
  );
}
export default MidRow;

// I put these utility functions outside of the component body since there is no real reason to recreate them on each render.

/**
 * @param {any[]} array
 */
function shuffleArray(array) {
  for (let i = array.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1));
    [array[i], array[j]] = [array[j], array[i]];
  }
  // Use slice instead of splice here to prevent mutating the original array
  return array.slice(0, 6);
}

/**
 * @param {string[]} array
 * @param {string} subs
 */
function countSubstrings(array, subs) {
  return array.filter(card => card.includes(subs)).length;
}
