import scrollToComponent from '../node_modules/react-scroll-to-component'
import RedPanda from './pngs/RedPanda.png';
import Sloth from './pngs/Sloth.png';
import FennecFox from './pngs/FennecFox.png';
import HoneyBadger from './pngs/HoneyBadger.png';
import Pangolin from './pngs/Pangolin.png';
import noviceImg from './pngs/pic4767592.png';
